import React from 'react';
import {Card,CardImg,CardText,CardTitle,CardSubtitle,CardBody} from 'reactstrap';
import {Loading} from './LoadingComponent';
import {baseUrl} from '../shared/baseUrl';
import {FadeTransform} from 'react-animation-components';
function RenderCard({item, isLoading, errMess}) {
    console.log('item is +'+item)
    console.log('isloading'+isLoading)
    console.log('errmess'+errMess)

    if (isLoading) {
        return(
                <Loading />
        );
    }
    else if (errMess) {
        return(
                <h4>{errMess}</h4>
        );
    }
    else{
        return(
            <FadeTransform in
                transformProps ={{
                    exitTransform:'scale(0.5) translateY'
                }}>
                <Card>
                    <CardImg src={baseUrl+item.image} alt={item.name} />
                    <CardBody>
                    <CardTitle>{item.name}</CardTitle>
                    {item.designation ? <CardSubtitle>{item.designation}</CardSubtitle> : null }
                    <CardText>{item.description}</CardText>
                    </CardBody>
                </Card>
              </FadeTransform>
        );
    }
}
function Home(props){
    return(
        <div className='container'>
           <div className='row align-items-start'>
               <div className='col-12 col-md-3 m-1'>
                   <RenderCard item={props.dish} 
                        isLoading={props.dishesLoading}
                        errMess={props.dishesErrMess}
                   />
                </div>                   
                 <div className='col-12 col-md-3 m-1'>
                   <RenderCard item={props.promotions} 
                        isLoading={props.promotionsLoading}
                        errMess={props.promotionsErrMess}
                   />
               </div>
               <div className='col-12 col-md-3 m-1'>
                   <RenderCard item={props.leader} 
                        isLoading={props.leaderLoading}
                        errMess={props.leaderErrMess}
                   />
               </div>
           </div>
        </div>
    );
}
export default Home;